.Carousel {
  display: flex;
  overflow-y: auto;
  margin-left: -10px;
  margin-right: -10px;
}

.Card {
  position: relative;
  flex-shrink: 0;
  height: 150px;
  width: 150px;
  margin-right: 10px;
  background-color: #3B7592;
  text-decoration: none;
}

.Card:hover {
  cursor: pointer;
}

.ButtonCard__Label {
  text-align: center;
  color: white;
}

.Card__Label {
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2em;
  align-items: center;
  padding-left: 5px;
  background-color: rgba(255,255,255,.5);
}

.ButtonCardContainer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ButtonCardContainer-Icon {
  color: white;
  font-size: 64px;
}
